<template>
<div class="screen screen-locked">
  <div class="logo">
    <img src="@/assets/images/logo.svg" alt="Been Together logo">
  </div>
  <div class="text">{{ message }}</div>
  <pin-display :value="pin"></pin-display>
  <pin-keyboard :max="4" v-model="pin"></pin-keyboard>
</div>
</template>

<script>
import PinDisplay from '@/components/PinDisplay'
import PinKeyboard from '@/components/PinKeyboard'
import { appState } from '@/store/app'

export default {
  name: 'LockedScreen',
  components: {
    PinKeyboard,
    PinDisplay
  },
  setup() {
    return {
      passCode: appState.pass_code,
    }
  },
  data() {
    return {
      pin: '',
      message: 'Input Pass Code'
    }
  },
  methods: {
    checkPin() {
      setTimeout(() => {
        if (this.pin == this.passCode) {
          this.$emit('ok')
        } else {
          this.message = 'Wrong Pass Code. Try again!'
        }
        this.pin = ''
      }, 300)
    }
  },
  watch: {
    pin(value) {
      if (value.length == 4) {
        this.checkPin(value)
      }
    }
  }
}
</script>