<template>
  <div class="pin-display">
    <v-icon color="#ffffff" v-for="(icon, index) in icons" :key="index">{{ icon }}</v-icon>
  </div>
</template>

<script>
export default {
  name: 'PinDisplay',
  props: {
    value: {
      type: String
    }
  },
  computed: {
    icons() {
      return this.value
        .padEnd(4, '*')
        .split('')
        .map(c =>
          c == '*'
            ? 'mdi-checkbox-blank-circle-outline'
            : 'mdi-checkbox-blank-circle'
        )
    }
  }
}
</script>

<style lang="scss">
.pin-display {
  height: 40px;
  text-align: center;
  margin: 20px 0;
  .v-icon {
    margin: 0 8px;
  }
}
</style>
