<template>
    <v-app>
        <main-screen></main-screen>
        <locked-screen @ok="locked = false" v-if="locked"></locked-screen>
        <splash-screen v-if="!ready" @ready="onReady"></splash-screen>
        <v-dialog v-model="dialog" width="250" persistent>
            <v-card class="text-center pt-5">
                <v-card-text>
                    New version is available
                    <div class="mt-3">
                        <v-btn @click="reloadApp" depressed color="success">Reload app</v-btn>
                    </div>
                </v-card-text>
            </v-card>
        </v-dialog>
    </v-app>
</template>

<script>
import { computed, onBeforeMount, onBeforeUnmount, ref } from '@vue/composition-api'
import { appState } from './store/app'
import { getCoupleDoc, restoreDataFromOldVersion, signInAnonymously } from './utils/app'

export default {
    name: 'App',
    setup() {
        const ready = ref(false)
        const onReady = () => ready.value = true

        const locked = ref(Boolean(appState.pass_code))
        const hasPassCode = computed(() => Boolean(appState.pass_code))
        const onBlur = () => (locked.value = hasPassCode.value)
        onBeforeMount(() => window.addEventListener('blur', onBlur))
        onBeforeUnmount(() => window.removeEventListener('blur', onBlur))

        const dialog = ref(false)
        const onServiceWorkerUpdated = () => (dialog.value = true)
        onBeforeMount(() => window.addEventListener('service-worker-updated', onServiceWorkerUpdated))
        onBeforeUnmount(() => window.removeEventListener('service-worker-updated', onServiceWorkerUpdated))
        const reloadApp = () => {
            this.dialog = false
            window.location.reload()
        }

        signInAnonymously().then(async (user) => {
            if (user) {
                const couple = await getCoupleDoc(user.uid)
                if (!couple) {
                    await restoreDataFromOldVersion()
                }
            } else {
                console.log('Missing user')
            }
        })

        return {
            ready,
            onReady,

            locked,
            dialog,

            reloadApp,
        }
    },
}
</script>