<template>
<div class="water-jar" @click="showDatePicker">
  <div class="water-filling">
    <div>In love</div>
    <div class="days">{{ days }}</div>
    <div>Days</div>
  </div>
  <div id="water" class="water" :style="styleObj">
    <svg viewBox="0 0 560 20" class="water_wave water_wave_back">
      <use xlink:href="#wave"></use>
    </svg>
    <svg viewBox="0 0 560 20" class="water_wave water_wave_front">
      <use xlink:href="#wave"></use>
    </svg>
  </div>

  <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" style="display: none;">
    <symbol id="wave">
      <path d="M420,20c21.5-0.4,38.8-2.5,51.1-4.5c13.4-2.2,26.5-5.2,27.3-5.4C514,6.5,518,4.7,528.5,2.7c7.1-1.3,17.9-2.8,31.5-2.7c0,0,0,0,0,0v20H420z"></path>
      <path d="M420,20c-21.5-0.4-38.8-2.5-51.1-4.5c-13.4-2.2-26.5-5.2-27.3-5.4C326,6.5,322,4.7,311.5,2.7C304.3,1.4,293.6-0.1,280,0c0,0,0,0,0,0v20H420z"></path>
      <path d="M140,20c21.5-0.4,38.8-2.5,51.1-4.5c13.4-2.2,26.5-5.2,27.3-5.4C234,6.5,238,4.7,248.5,2.7c7.1-1.3,17.9-2.8,31.5-2.7c0,0,0,0,0,0v20H140z"></path>
      <path d="M140,20c-21.5-0.4-38.8-2.5-51.1-4.5c-13.4-2.2-26.5-5.2-27.3-5.4C46,6.5,42,4.7,31.5,2.7C24.3,1.4,13.6-0.1,0,0c0,0,0,0,0,0l0,20H140z"></path>
    </symbol>
  </svg>

  <v-dialog v-model="modal" width="290px" ref="dialog">
    <v-date-picker scrollable v-model="date">
      <v-spacer></v-spacer>
      <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
      <v-btn text color="primary" @click="setDate(date)">OK</v-btn>
    </v-date-picker>
  </v-dialog>
</div>
</template>

<script>
import moment from 'moment'
import { coupleState } from '@/store/couple'
import { computed, ref } from '@vue/composition-api'

export default {
  setup() {
    const modal = ref(false)
    const date = ref(moment().format('YYYY-MM-DD'))

    return {
      modal,
      date,
      start_day: computed(() => coupleState.start_day),
    }
  },
  computed: {
    days() {
      return moment().diff(moment(this.start_day), 'days') + 1
    },
    styleObj() {
      let value = Math.min(98, (this.days / 3600) * 100)
      let percent = Math.max(5, 100 - value)

      return {
        transform: 'translate(0, ' + percent +'%)'
      }
    }
  },
  methods: {
    showDatePicker() {
      this.date = moment(this.start_day).format('YYYY-MM-DD')
      this.modal = true
    },
    setDate(val) {
      coupleState.start_day = val
      this.modal = false
    }
  }
}
</script>

<style lang="scss">
.water-jar {
  position: relative;
  height: 180px;
  width: 180px;
  border: 2px solid hsla(0, 0%, 100%, 0.5);
  border-radius: 100%;
  overflow: hidden;
  transform: translate(0);
  margin-left: auto;
  margin-right: auto;

 .water-filling {
    padding-top: 30px;
    text-align: center;
    width: 100%;
    height: 100%;
    color: #ffffff;
    font-family: 'Pacifico', cursive;
    position: absolute;
    z-index: 3;

    .days {
      // -webkit-text-stroke: 1px #ff30a2;
      font-size: 50px;
    }
  }
  .water {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    transform: translate(0, 100%);
    background: #ff1094;
    opacity: .8;
  }

  .water_wave {
    width: 200%;
    position: absolute;
    bottom: 100%;
  }

  .water_wave_back {
    right: 0;
    fill: #ff5fdc;
    animation: wave-back 1.4s infinite linear;
  }

  .water_wave_front {
    left: 0;
    fill: #ff1094;
    margin-bottom: -1px;
    animation: wave-front .7s infinite linear;
  }
}

@keyframes wave-front {
  100% {
    transform: translate(-50%, 0);
  }
}
@keyframes wave-back {
  100% {
    transform: translate(50%, 0);
  }
}
</style>