<template>
<div class="count-units">
  <div class="header">We have been together...</div>
  <div class="units">
    <div>
      <div class="mdi mdi-heart-outline">{{ units.years }}</div>
      <div class="name">Year</div>
    </div>
    <div>
      <div class="mdi mdi-heart-outline">{{ units.months }}</div>
      <div class="name">Month</div>
    </div>
    <div>
      <div class="mdi mdi-heart-outline">{{ units.days }}</div>
      <div class="name">Day</div>
    </div>
    <div>
      <div class="mdi mdi-heart-outline">{{ units.hours }}</div>
      <div class="name">Hour</div>
    </div>
    <div>
      <div class="mdi mdi-heart-outline">{{ units.minutes }}</div>
      <div class="name">Min</div>
    </div>
    <div>
      <div class="mdi mdi-heart-outline">{{ units.seconds }}</div>
      <div class="name">Sec</div>
    </div>
  </div>
</div>
</template>

<script>
import moment from 'moment'
import { coupleState } from '@/store/couple'
import { computed } from '@vue/composition-api'

const SECOND  = 1
const MINUTE  = 60 * SECOND
const HOUR    = 60 * MINUTE
const DAY     = 24 * HOUR

export default {
  name: 'CountUnits',
  setup() {
    return {
      start_day: computed(() => coupleState.start_day),
    }
  },
  data() {
    return {
      interval: null,
      units: {
        years: 0,
        months: 0,
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0
      }
    }
  },
  methods: {
    update() {
      const start = moment(this.start_day)
      const now   = moment()

      this.units.years = now.diff(start, 'years')
      this.units.months = now.diff(start.add(this.units.years, 'years'), 'months')
      this.units.days = now.diff(start.add(this.units.months, 'months'), 'days')
      let seconds = Math.floor(moment().diff(moment(this.start_day)) / 1000)

      this.units.hours = Math.floor((seconds % DAY) / HOUR)
      this.units.minutes = Math.floor((seconds % HOUR) / MINUTE)
      this.units.seconds = seconds % MINUTE
    }
  },
  mounted() {
    this.update()
  },
  created() {
    this.interval = setInterval(this.update, 1000)
  },
  beforeDestroy() {
    if (this.interval) {
      clearInterval(this.interval)
    }
  }
}
</script>

