import localforage from '@/plugins/localforage'
import { db, firebaseApp } from '@/plugins/firebase'
import { coupleState } from '@/store/couple'
import { appState } from '@/store/app'

export const signInAnonymously = async () => {
    try {
        const auth = await firebaseApp.auth().signInAnonymously()
        if (auth.user) {
            appState.user = auth.user
        }
        return auth.user
    } catch (error) {
        return null
    }
}

export const getCoupleDoc = async uid => {
    try {
        const snapshot = await db
            .collection('couples')
            .where('user1.uid', '==', uid)
            .limit(1)
            .get(1)
        if (snapshot.docs.length) {
            appState.coupleRef = snapshot.docs[0].ref
            const doc = snapshot.docs[0].data()
            coupleState.start_day = doc.start_day
            coupleState.user1 = doc.user1
            coupleState.user2 = doc.user2
            return doc
        }
    } catch (error) {
        console.log(error)
    }
}

export const createCoupleDoc = async () => {
    try {
        await db.collection('couples').add(coupleState)
    } catch (error) {
        console.log(error)
    }
}

export const updateCoupleDoc = () => {
    if (appState.coupleRef) {
        appState.coupleRef.update(coupleState)
    }
}

export const restoreDataFromOldVersion = async () => {
    const oldStore = await localforage.getItem('vuex')
    if (oldStore) {
        if (oldStore.start_day) coupleState.start_day = oldStore.start_day
        if (oldStore.user1) {
            oldStore.user1.uid = appState.user.uid
            oldStore.user1.avatar = ''
            coupleState.user1 = oldStore.user1
        }
        if (oldStore.user2) {
            oldStore.user2.avatar = ''
            coupleState.user2 = oldStore.user2
        }
    }
}
