          <v-list-item-content>Pass code setting</v-list-item-content>
        </v-list-item>
      </v-list>
    </v-bottom-sheet>
  </div>
</template>

<script>
export default {
  name: 'AppMenu',
  data() {
    return {
      menu: false
    }
  },
  methods: {
    showPassCode() {
      this.menu = false
      this.$emit('showSettingPassCode')
    }
  }
}
</script>

