<template>
  <div class="pin-keyboard">
    <div class="kb-row">
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('1')"><v-icon>mdi-numeric-1</v-icon></v-btn>
      </div>
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('2')"><v-icon>mdi-numeric-2</v-icon></v-btn>
      </div>
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('3')"><v-icon>mdi-numeric-3</v-icon></v-btn>
      </div>
    </div>
    <div class="kb-row">
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('4')"><v-icon>mdi-numeric-4</v-icon></v-btn>
      </div>
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('5')"><v-icon>mdi-numeric-5</v-icon></v-btn>
      </div>
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('6')"><v-icon>mdi-numeric-6</v-icon></v-btn>
      </div>
    </div>
    <div class="kb-row">
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('7')"><v-icon>mdi-numeric-7</v-icon></v-btn>
      </div>
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('8')"><v-icon>mdi-numeric-8</v-icon></v-btn>
      </div>
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('9')"><v-icon>mdi-numeric-9</v-icon></v-btn>
      </div>
    </div>
    <div class="kb-row">
      <div class="num"></div>
      <div class="num">
        <v-btn icon x-large color="#ffffff" @click="input('0')"><v-icon>mdi-numeric-0</v-icon></v-btn>
      </div>
      <div class="num">
        <v-fade-transition>
          <v-btn icon x-large color="#ffffff" @click="backspace" v-show="value.length"><v-icon small>mdi-backspace</v-icon></v-btn>
        </v-fade-transition>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PinKeyboard',
  props: {
    value: {
      type: String,
      default: ''
    },
    max: {
      type: Number,
      default: 4
    }
  },
  methods: {
    input(num) {
      if (this.value.length < this.max) {
        this.$emit('input', this.value + num)
      }
    },
    backspace() {
      if (this.value.length) {
        this.$emit('input', this.value.slice(0, this.value.length - 1))
      }
    }
  }
}
</script>

<style lang="scss">
.pin-keyboard {
  max-width: 290px;
  margin: 0 auto;
  color: #ffffff;

  .kb-row {
    display: flex;
    margin: 4px 0;

    .num {
      text-align: center;
      flex: 1;
    }
  }
}
</style>