<template>
<div class="screen screen-passcode-setting">
  <v-toolbar flat color="transparent" dark>
    <v-btn icon @click="$emit('input', false)">
      <v-icon>mdi-arrow-left</v-icon>
    </v-btn>
    <v-toolbar-title>Pass code setting</v-toolbar-title>
  </v-toolbar>
  <div class="logo">
    <img src="@/assets/images/logo.svg" alt="Been Together logo">
  </div>
  <div class="text">{{ message }}</div>
  <pin-display :value="pin"></pin-display>
  <pin-keyboard :max="4" v-model="pin"></pin-keyboard>

  <div class="text-center mt-5" v-if="passCode">
    <v-btn outlined color="orange" @click="removePassCode">Remove Pass Code</v-btn>
  </div>
</div>
</template>

<script>
import PinDisplay from '@/components/PinDisplay'
import PinKeyboard from '@/components/PinKeyboard'
import { appState } from '@/store/app'

export default {
  name: 'PassCodeSettingScreen',
  props: {
    value: {
      type: Boolean,
      required: true
    }
  },
  components: {
    PinKeyboard,
    PinDisplay
  },
  setup() {
    return {
      passCode: appState.pass_code,
    }
  },
  data() {
    return {
      checking: false,
      pin: '',
      pinNew: '',
      message: 'Input New Pass Code'
    }
  },
  methods: {
    checkPin() {
      this.checking = true
      if (!this.pinNew) {
        this.pinNew = this.pin
        this.pin = ''
        this.message = 'Re-input Pass Code'
      } else {
        if (this.pin == this.pinNew) {
          appState.pass_code = this.pin
          this.$emit('input', false)
        } else {
          this.pinNew = ''
          this.pin = ''
          this.message = 'Wrong Pass Code. Please try again!'
        }
      }
    },
    removePassCode() {
      appState.pass_code = ''
      this.$emit('input', false)
    }
  },
  watch: {
    pin(value) {
      if (value.length == 4) {
        this.checkPin(value)
      }
    }
  }
}
</script>