<template>
<div class="screen screen-splash" :style="objStyle">
  <v-fade-transition>
    <div class="logo" v-show="n > 0">
      <img src="@/assets/images/logo-white.svg" alt="Been Together logo">
    </div>
  </v-fade-transition>
  <v-fade-transition>
    <div class="app-title" v-show="n > 1">Been Together</div>
  </v-fade-transition>
</div>
</template>

<script>
export default {
  name: 'SplashScreen',
  data: () => ({
    n: 0
  }),
  computed: {
    objStyle() {
      let styles = {}
      styles.backgroundColor = '#ffffff'
      return styles
    }
  },
  mounted() {
    let interval = setInterval(() => {
      this.n += 1
      if (this.n > 5) {
        clearInterval(interval)
        this.$emit('ready', true)
      }
    }, 30)
  }
}
</script>

<style lang="scss">
.screen-splash {
	background: linear-gradient(135deg, #47ceff, #2f89ff, #23a6d5, #4bcfff);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
</style>